<template>
  <div id="chartBox">

  </div>

</template>
<script>
//  ECharts 
var echarts = require('echarts/lib/echarts');
require('echarts/lib/chart/line');
require('echarts/lib/component/legend');
require('echarts/lib/component/tooltip');
require('echarts/lib/component/visualMap');
require('echarts/lib/component/markLine');

import {
  mapGetters,
  mapMutations,
  mapActions
} from 'vuex'
export default {
  props: {
    weights: Array
  },
  mounted () {
    if (this.weights.length) {
      this.draw()
    }
  },
  watch: {
    weights: {
      handler (curVal, oldVal) {
        if (curVal.length) {
          this.draw()
        }
      },
      deep: true
    },
    userInfo: {
      handler () {
        this.draw()
      },
      deep: true
    }
  },
  computed: {
    weightall () {
      let temp = []
      this.weights.map(item => {
        temp.push(item.AM * 1, item.PM * 1)
      })
      temp.push(this.idealWeight, this.recommondWeight)
      return temp.filter(item => item > 0).sort((a, b) => a - b)
    },
    min () {
      return this.weightall[0] * 1
    },
    max () {
      return this.weightall[this.weightall.length - 1] * 1
    },
    idealWeight () {
      return this.userInfo.idealWeight ? this.userInfo.idealWeight : 0
    },
    height () {
      return this.userInfo.height ? this.userInfo.height : 0
    },
    normalWeight () {
      let temp = []
      let _w = this.height * this.height * 0.0001
      return [(18.5 * _w).toFixed(1), (24 * _w).toFixed(1)]
    },
    recommondWeight () {
      let _w = this.height * this.height * 0.0001
      return (21.5 * _w).toFixed(1)
    },
    standerds () {
      return [this.idealWeight * 1, this.recommondWeight * 1].sort((a, b) => a.toFixed(1) - b.toFixed(1))
    },
    ...mapGetters(['userInfo'])
  },
  methods: {
    draw () {
      // 
      // 
      // domecharts
      let myChart = echarts.init(document.getElementById('chartBox'))
      // 
      myChart.setOption({
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          right: 0,
          top: 0,
          orient: 'vertical',
          tooltip: {
            show: true
          }
        },
        xAxis: {
          data: this.weights.map(item => item.date)
        },
        yAxis: {
          splitLine: {
            show: false
          },
          min: this.min,
          max: this.max
        },
        visualMap: {
          show: this.idealWeight > 0 ? true : false,
          top: 0,
          left: 0,
          orient: 'horizontal',
          pieces: this.standerds[0] != this.standerds[1] ? [{
            gt: 0,
            lte: this.standerds[0],
            color: 'aqua'
          }, {
            gt: this.standerds[0],
            lte: this.standerds[1],
            color: '#ffde33'
          }, {
            gt: this.standerds[1],
            color: '#f56c6c'
          }] : [{
            gt: 0,
            lte: this.standerds[0],
            color: 'aqua'
          }, {
            gt: this.standerds[1],
            color: '#f56c6c'
          }]
        },
        series: [
          {
            name: '',
            type: 'line',
            smooth: true,
            connectNulls: true,
            data: this.weights.map(function (item) {
              return item.AM
            }),
            symbol: 'circle',
            symbolSize: [8, 8],
            markLine: {
              silent: true,
              data: this.recommondWeight != this.idealWeight ? [
                {
                  yAxis: this.idealWeight,
                  label: {
                    position: 'middle',
                    formatter: ` ${this.idealWeight}kg`
                  },
                  lineStyle: {
                    color: this.recommondWeight > this.idealWeight ? 'aqua' : '#ffde33'
                  }
                },
                {
                  yAxis: this.recommondWeight,
                  label: {
                    position: 'middle',
                    formatter: ` ${this.recommondWeight}kg`
                  },
                  lineStyle: {
                    color: this.recommondWeight < this.idealWeight ? 'aqua' : '#ffde33'
                  }
                }] : [{
                  yAxis: this.idealWeight,
                  label: {
                    position: 'middle',
                    formatter: ` ${this.idealWeight}kg`
                  },
                  lineStyle: {
                    color: 'aqua'
                  }
                }
                ]
            }
          },
          {
            name: '',
            type: 'line',
            smooth: true,
            connectNulls: true,
            data: this.weights.map(function (item) {
              return item.PM
            }),
            symbol: 'rect',
            symbolSize: [8, 8],
            markLine: {
              silent: true,
              data: this.recommondWeight != this.idealWeight ? [
                {
                  yAxis: this.idealWeight,
                  label: {
                    position: 'middle',
                    formatter: ` ${this.idealWeight}kg`
                  },
                  lineStyle: {
                    color: this.recommondWeight > this.idealWeight ? 'aqua' : '#ffde33'
                  }
                },
                {
                  yAxis: this.recommondWeight,
                  label: {
                    position: 'middle',
                    formatter: ` ${this.recommondWeight}kg`
                  },
                  lineStyle: {
                    color: this.recommondWeight < this.idealWeight ? 'aqua' : '#ffde33'
                  }
                }] : [{
                  yAxis: this.idealWeight,
                  label: {
                    position: 'middle',
                    formatter: ` ${this.idealWeight}kg`
                  },
                  lineStyle: {
                    color: 'aqua'
                  }
                }
                ]
            }
          }
        ],

      })
    }
  }
}
</script>

<style lang="scss" scoped>
#chartBox {
    width: 100%;
    height: 750px;
}
</style>



